<template>
  <div id="app">
    <wlExplorer
      ref="wl-explorer-cpt"
      :headerDropdown="headerHandle"
      :columns="file_table_columns"
      :all-path="all_folder_list"
      :is-folder-fn="isFolderFn"
      :folderType="rource_type"
      :data="file_table_data"
      :props="explorer_prop"
      @handleFolder="handleFolder"
      @search="fileSearch"
      @del="fileDel"
      @closeFade="closeOtherLayout(fade)"
    ></wlExplorer>
  </div>
</template>

<script>
import wlExplorer from '@/pages/WlExplorer'; // 
import { closeOtherLayout, arrayToTree } from '@/util'; // 
import { 
  getFileListApi, // 1
  getAllFoldersApi, // 4
  delFileApi, // 6|
} from '@/api'; // 
const apiok = 200;

export default {
  name: 'app',
  components: {
    wlExplorer
  },
  data() {
    const _GB = 1024*1024;
    // const vm = this;
    return {
      headerHandle: [
        { name: '', command: 'auth'},
      ], // -
      file_table_columns: [
        {
          label: '', 
          prop: 'Name', 
          minWidth: 120
        },
        {
          label: '', 
          align: 'center', 
          width: 120,
          formatter(row){
            return row.EditTime.split('T')[0] || '-'
          }
        },
        {
          label: '', 
          align: 'center',
          width: 90,
          formatter(row){
            return row.Type === 1 ? '' : row.SuffixName;
          }
        },
        {
          label: '', 
          minWidth: 90,
          align: 'center', 
          formatter(row){
            if(row.Size === null) return '-'
            if(row.Size < 1024){ // 1024kb
              return row.Size + 'KB';
            }
            if(row.Size < _GB){ // 1024*1024
              let _mb = (row.Size/1024).toFixed(2);
              return  parseFloat(_mb) + 'MB';
            }
            let _gb = (row.Size/_GB).toFixed(2);
            return parseFloat(_gb) + 'GB';
          }
        },
        {
          label: '', 
          align: 'center', 
          width: 120,
          formatter(row){
            return row.CreateTime.split('T')[0] || '-'
          }
        },
        {
          label: '', 
          minWidth: 100,          
          align: 'center', 
          formatter(row){
            return row.CreateUserName || '-'
          }
        },
        {
          label: '', 
          minWidth: 100,          
          formatter(row){
            return row.Describe || '-'
          }
        },
      ], // 
      file_table_data: [], //       
      all_folder_list: [], // 
      tree_folder_list: [], // 
      type: {
        folder: 1,
        img: 2,
        video: 3,
        other: 4
      }, // 
      rource_type: {
        self: 1, // 
      }, // 
      explorer_prop: {
        name: "Name", 
        match: "Name", 
        splic: true,
        suffix: "SuffixName",
        pathId: "Id",
        pathPid: "ParentId",
        pathName: "Name",
      }, // 
    }
  },
  methods: {
    /**
     * 
     * file: Object 
     * update: Boolean 
     */
    fileSearch(file, update){
      if(update){
        this.path = file;
        this.getFileList();
      }
    },
    // 
    getFileList(){
      getFileListApi().then(({data}) => {
        if(data.StatusCode === apiok){
          this.file_table_data = data.Data || [];
        }
      })
    },
    /**
     * 
     * act:Object 
     * type:String  addedit
     * file:Object 
     * auth:Boolean 
     */
    handleFolder(act, type, file, auth){
      this.path = file;
      this.just_edit_auth = auth;
      this.fade.folder = true;
      if(type === 'add'){
        this.$refs['folder_form'].resetFields();
        this.folder_form.Id = '';
        this.folder_form.ParentId = file.id;
        this.folder_form.RourceType = this.rource_type.self;
        return;
      }
      this.child_act_saved = act;
    },
    // 
    fileDel(data){
      let cannot_del_data = []; // 
      let normal_data_file = []; // 
      let normal_data_folder = []; // 
      data.forEach(i => {
        i.RourceType !== this.rource_type.self
          ? cannot_del_data.push(i) // 
          : i.Type === this.type.folder 
            ? normal_data_folder.push(i.Id) // 
            : normal_data_file.push(i.Id) // 
      })
      // 
      if(cannot_del_data.length > 0){
        let _msg = '<p class="title"></p>';
        cannot_del_data.forEach(i => {
          _msg += `<p class="msg">${i.Name}</p>`
        })
        this.$message({
          dangerouslyUseHTMLString: true,
          showClose: true,
          message: _msg, 
          type: "warning",
          customClass: 'mulit-msg',
        });
      }
      if(normal_data_folder.length === 0 && normal_data_file.length === 0) return;
      // 
      let _data = {
        FolderIds: normal_data_folder,
        FolderFileIds: normal_data_file,
      }
      delFileApi(_data).then(({data}) => {
        if(data.StatusCode === apiok){
          this.file_table_data = this.file_table_data.filter(i => ![...normal_data_file, ...normal_data_folder].includes(i.Id));
          this.$message({
            showClose: true,
            message: data.Message,
            type: "success"
          });
        }
      })
    },
    // 
    getAllFolders(){
      getAllFoldersApi().then(({data}) => {
        if(data.StatusCode === apiok){
          this.all_folder_list = data.Data || [];
          let _list = [...this.all_folder_list];
          let options = {
            id: this.explorer_prop.pathId,
            pid: this.explorer_prop.pathPid,
            children: 'Children',
          }
          this.tree_folder_list = arrayToTree(_list, options)
        }
      })
    },
    // 
    isFolderFn(row){
      return row.Type === this.type.folder;
    }
  },
  created() {
    this.closeOtherLayout = closeOtherLayout;
    this.getAllFolders();
    this.getFileList();    
  },
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  position: absolute;
  top:0;
  left:0;
  padding: 25px;
  width: 100%;
  height: 100%;
  background: #f7f7f7;
  box-sizing: border-box;
}
</style>
